import React from 'react';
import {useFieldValue, useFireBaseFirestore} from "./FireBaseProvider";

const CourseContext = React.createContext();

const collections = ['Courses', 'ModuleGroups', 'Modules', 'Exercises'];

const getParentCollection = (collection) => {
    const childIndex = collections.findIndex(element => element === collection);

    return collections[childIndex - 1];
};

const sortByArray = (data, array) => {
    let sorted = [];
    let temp = {};

    if(data.length === 0) return {};

    for(let i = 0; i < data.length; i++) {
        temp = {...temp, ...data[i]};
    };

    for(let i = 0; i < array.length; i++) {
        sorted.push({id: array[i], ...temp[array[i]]});
    };

    return sorted;
};

export const useAddData = () => {
    const Firestore = useFireBaseFirestore();
    const FieldValue = useFieldValue();

    const add = (collection, data, id) => {
        if(collection === 'Courses') {
            Firestore.collection(collection).add(data);
        } else {
            Firestore.collection(collection).add({parent: id, ...data}).then(doc => {
                Firestore.collection(getParentCollection(collection)).doc(id).update({
                    sort: FieldValue.arrayUnion(doc.id)
                });
            });
        }
    };

    return add;
};

export const useUpdateData = () => {
    const Firestore = useFireBaseFirestore();

    const update = (collection, data, id) => {
        Firestore.collection(collection).doc(id).update(data);
    };

    return update;
};

export const useDeleteData = () => {
    const Firestore = useFireBaseFirestore();
    const FieldValue = useFieldValue();

    const remove = (collection, id) => {
        switch(collection) {
            case 'Courses':
                Firestore.collection(collection).doc(id).get().then(doc => {
                    Firestore.collection(collection).doc(id).delete();

                    let {sort} = doc.data();

                    if(sort === undefined) return;

                    sort.forEach(item => {
                        remove('ModuleGroups', item);
                    })
                });
                break;
            case 'ModuleGroups':
                Firestore.collection(collection).doc(id).get().then(doc => {
                    Firestore.collection(collection).doc(id).delete();

                    let {sort, parent} = doc.data();

                    console.log(parent);

                    Firestore.collection('Courses').doc(parent).update({
                        sort: FieldValue.arrayRemove(id)
                    });

                    if(sort === undefined) return;

                    sort.forEach(item => {
                        remove('Modules', item);
                    })
                });
                break;
            case 'Modules':
                Firestore.collection(collection).doc(id).get().then(doc => {
                    Firestore.collection(collection).doc(id).delete();

                    let {sort, parent} = doc.data();

                    Firestore.collection('ModuleGroups').doc(parent).update({
                        sort: FieldValue.arrayRemove(id)
                    });

                    if(sort === undefined) return;

                    sort.forEach(item => {
                        remove('Exercises', item);
                    })
                });
                break;
            case 'Exercises':
                Firestore.collection(collection).doc(id).get().then(doc => {
                    Firestore.collection(collection).doc(id).delete();

                    const {parent} = doc.data();

                    Firestore.collection('Modules').doc(parent).update({
                        sort: FieldValue.arrayRemove(id)
                    });
                })

                break;
            default:
                return null;
        }
    }

    return remove;
};

export const useGetData = (collection, id) => {
    const [state, setState] = React.useState({loading: false});
    const Firestore = useFireBaseFirestore();

    React.useEffect(() => {
        if(state?.loading !== undefined) {
            if(collection === 'Courses') {
                Firestore.collection(collection).get().then(snapshot => {
                    setState(snapshot.docs.map(doc => ({id: doc.id, ...doc.data()})));
                });
            } else if(collection === 'Exercise' && id !== undefined) {
                Firestore.collection('Exercises').doc(id).get().then(doc => {setState(doc.data())})
            } else {
                if(id === undefined) return;
                Promise.all([
                    Firestore.collection(collection)
                        .where('parent', '==', id)
                        .get()
                        .then(snapshot => snapshot.docs.map(doc => ({[doc.id]: doc.data()}))),
                    Firestore.collection(getParentCollection(collection)).doc(id).get().then(doc => doc.data())
                ]).then(queries => {
                    setState(sortByArray(queries[0], queries[1]?.sort));
                });
            }
        }

    }, [state, Firestore, collection, id]);

    return state;
};

const CourseProvider = ({children}) => {
    const [state, setState] = React.useState({});

    return <CourseContext.Provider value={[state, setState]}>{children}</CourseContext.Provider>
};

export default CourseProvider;