import React from 'react';
import NavBar from "../../NavBar";
import Container from "../../Atomics/Container";
import BottomBar from "../../BottomBar";
import {Route, Switch} from "react-router-dom";
import LoadingCircle from "../../Atomics/LoadingCircle";
import CourseProvider from "../../Providers/CourseProvider";

const Feedback = React.lazy(() => import("./Feedback"));
const Forum = React.lazy(() => import("./Forum"));
const Home = React.lazy(() => import('./Home'));
const Notes = React.lazy(() => import('./Notes'));
const Profile = React.lazy(() => import("./Profile"));
const Search = React.lazy(() => import("./Search"));
const Settings = React.lazy(() => import("./Settings"));
const Course = React.lazy(() => import("./Course"));
const Module = React.lazy(() => import("./Module"));
const Exercise = React.lazy(() => import("./Exercise"));
const CMS = React.lazy(() => import('../../CMS'));

const Protected = () => {
    return (
        <CourseProvider>
            <NavBar/>
            <Container style={{minHeight: 'calc(100vh - 129px)'}}>
                <React.Suspense fallback={<LoadingCircle />}>
                    <Switch>
                        <Route exact path={'/feedback'}>
                            <Feedback />
                        </Route>
                        <Route exact path={'/forum'}>
                            <Forum />
                        </Route>
                        <Route exact path={'/'}>
                            <Home />
                        </Route>
                        <Route exact path={'/notes'}>
                            <Notes />
                        </Route>
                        <Route exact path={'/profile'}>
                            <Profile />
                        </Route>
                        <Route exact path={'/search'}>
                            <Search />
                        </Route>
                        <Route exact path={'/settings'}>
                            <Settings />
                        </Route>
                        <Route exact path={'/course/:courseId'}>
                            <Course />
                        </Route>
                        <Route exact path={'/course/:courseId/:moduleGroupId/:moduleId'}>
                            <Module />
                        </Route>
                        <Route exact path={'/course/:courseId/:moduleGroupId/:moduleId/:exerciseId'}>
                            <Exercise />
                        </Route>
                        <Route exact path={'/CMS'}>
                            <CMS/>
                        </Route>
                    </Switch>
                </React.Suspense>
            </Container>
            <BottomBar/>
        </CourseProvider>
    )
};

export default Protected;